	module.exports = {"data":{"markdownRemark":{"html":"<p>シンポジウムに参加するために上海師範大学に行く、霧、車は非常にオープンすることは困難です。会場は上海師範大学タリウリジュでした。約80名がフォーラムに参加し、そのほとんどは上海大学の校長と党書記であり、個々の教師がいました。</p>\n<p>北京指導者の演説は、中国の現在の発展が最も才能があると述べた。中国は依然として貧しい国ですが、米国、ドイツ、日本に追いつくのに時間がかかります。 Lの祖父は地方の首都である江蘇省の中学校の校長だった。国民党の知事が最初に就任したのは、彼の祖父を訪問することだった。 Lは、国民党はまだ教師を尊重することができると言った、共産党はそれをしないでください？人がより大きく、教師を尊敬していると言います。正式な立場は学校に入ることができません。浙江省は、教師の給与のルールと延滞を設定しています。建物をカバーすることはできませんし、車を買うこともできず、海外に行くこともできません。</p>\n<p>中国は世界で最大の教育機関であり、2億人が学校に通っていません。昨年、北京大学図書館は雑誌を保護するために本を買わなかった。北京の指導者は基本的な教科書と統一された教科書に反対し、創造性はないと言った。第二に、学校に大きな改革はない、古い顔は \"211プロジェクト\"に入ることはできません、第三は関連性を奨励することである \"211プロジェクト\"について話すとき、3つの条件を追加する必要があると言われています。学校は共同して「211プロジェクト」を設立した。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0006.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0006.md","frontmatter":{"title":"1月9日 日曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0006.md absPath of file >>> MarkdownRemark"}}

